.Sidebar {
  width: 200px;
  background-color: #FFFFFF;
  display: flex;
  flex-direction: column;
  border-right: 1px solid #E7E7E7;
  color: #71767A;
}

.Text {
  margin: 0;
  padding-left: 20px;
}

.Column {
  display: flex;
  align-items: center;
  height: 30px;
}

.Title {
  margin: 0;
  font-weight: bold;
  font-size: 14px;
  margin-top: 10px;
  margin-bottom: 10px;
  padding-left: 20px;
}

.Selected {
  background-color: #E8364B;
  color: #FCF3F6;
}

.TextSelected {
  color: #FCF3F6;
}


