.SongContainer {
  margin-left: 20px;
  margin-right: 20px;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  max-height: 70%;
  min-width: 0px;
}

.SongContainerListItem {
  display: flex;
  flex: 1;
}

.SongContainerName {
  width: 45%;
  max-width: 50%;
  overflow: hidden;
}

.SongContainerArtist {
  width: 20%;
  max-width: 20%;
  overflow: hidden;
}

.SongContainerAlbum {
  width: 25%;
  max-width: 25%;
  overflow: hidden;
  text-overflow: ellipsis;
}

.SongContainerTIme {
  width: 5%;
  max-width: 5%;
  overflow: hidden;
}

table {
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: 100%;
}

tbody, thead tr {
  display: table;
  width: 100%;
  table-layout: fixed;
}

thead {
  display: block;
  position: sticky;
  top: 0;
  background-color: white;
  display :block;
}

td, th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}




