.Card {
  width: 200px;
  height: 250px;
  margin-bottom: 10px;
  margin-right: 10px;
}

.Artwork {
  width: 200px;
  height: 200px;
}

.ArtworkImage {
  width: 200px;
  height: 200px;
  border-radius: 8px;
}

.Name {
  padding-top: 5px;
  padding-bottom: 5px;
  height: 20px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.ArtistName {
  color: #7E7E7E;
  height: 20px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.ContextMenu {
  position: relative;
  width: 120px;
  height: auto;
  background-color: white;
  flex-direction: column;
  border-radius: 8px;
}

.ContextMenuItem {
  height: 20px;
  border-bottom: 1px solid #E7E7E7;
  padding: 10px;
  transition: 0.3s;
}

.ContextMenuTop {
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

.ContextMenuBottom {
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
}

.ContextMenuItem:hover {
  color: white;
  background-color: #EC5369;
}


