.View {
  flex: 1;
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  min-width: 0px;
}

.ViewArtwork {
  width: 200px;
  height: 200px;
  margin-left: 20px;
  border-radius: 8px;
}

.ViewAttributes {
  margin-left: 20px;
}

.ViewAttributesText {
  font-weight: bolder;
  font-size: 36px;
}

.ViewData {
  display: flex;
  flex-direction: row;
  min-width: 0px;
  border-radius: 8px;
}


