.Player {
  height: 50px;
  min-height: 50px;
  /* Base: Top: */
  background-color: #FFFFFF;
  border-radius: 4px 0 0 4px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #E7E7E7;
}

.HeaderText {
  font-weight: normal;
  font-size: 24px;
  position: absolute;
  top: 10px;
  left: 20px;
  font-weight: 500;
}

.TextRed {
  color: #EC5369;
}

.NowPlaying {
  display: flex;
  flex-direction: row;
  width: 30%;
  background-color: #FFFFFF;
  height: 100%;
  min-width: 0;
  border: 1px solid #E7E7E7;
}

.NowPlayingArtwork {
  height: 50px;
  align-self: flex-start;
}

.NowPlayingContainer {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 0;
  margin-top: 0;
}

.NowPlayingAttributes {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 100%;
  min-width: 0;
  height: 30px !important;
}

.NowPlayingText {
  max-width: 70%;
  height: 20px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-size: 15px;
}

.NowPlayingProgress {
  flex: 1;
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: space-between;
}

.NowPlayingTime {
  font-size: 12px;
  flex: 1;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-left: 5px;
  margin-right: 5px;
}

.NowPlayingProgressBarBackground {
  width: 100%;
  height: 5px;
  background-color: #E7E7E7;
}

.NowPlayingProgressBar {
  height: 100%;
  background-color: #EC5369;
}

.PlayerControls {
  position: absolute;
  top: 0px;
  left: 20%;
  width: 8%;
  height: 50px !important;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.PlayerControlsIcon {
  width: 35px;
  height: 35px;
}

.unauthorize {
  position: absolute;
  top: 15px;
  right: 20%;
  width: auto;
}


